@import '../../styles/_color-themes.scss'
@import '../../styles/mixins.sass'

.cta-content
    margin-top: 120px
    @media ( max-width: $screen-small-max)
        margin-top: 40px
    .main-info
        width: 712px //todo S size repl
        @media (min-width: $screen-medium-min)
            width: 619px
            min-width: 619px
        @media (min-width: $screen-large-min)
            width: 712px
        @media (min-width: $screen-extra-large-min)
            padding-top: 40px
        h1
            margin-bottom: 20px
        .input-with-button
            margin-top: 40px
            margin-bottom: 80px
            @media ( max-width: $screen-small-max)
                margin-bottom: 60px
            .btn-start
                @media ( max-width: $screen-small-max)
                    width: 144px
        .icons-panel
            gap: 28px
    .illustration-part
        display: block
        @media ( max-width: $screen-small-max)
            display: none
        @media (min-width: $screen-medium-min)
            margin-left: -106px
        @media (min-width: $screen-large-min)
            margin-right: -50px